/**
 * AI-powered background removal using Hugging Face Transformers
 * Runs entirely in the browser with WebGPU acceleration
 */

import { pipeline, env } from "@huggingface/transformers";

// Configure transformers.js
env.allowLocalModels = false;
env.useBrowserCache = false;

const MAX_IMAGE_DIMENSION = 1024;

/**
 * Resize image if it exceeds max dimensions
 */
function resizeImageIfNeeded(
  canvas: HTMLCanvasElement,
  ctx: CanvasRenderingContext2D,
  image: HTMLImageElement
): boolean {
  let width = image.naturalWidth;
  let height = image.naturalHeight;

  if (width > MAX_IMAGE_DIMENSION || height > MAX_IMAGE_DIMENSION) {
    if (width > height) {
      height = Math.round((height * MAX_IMAGE_DIMENSION) / width);
      width = MAX_IMAGE_DIMENSION;
    } else {
      width = Math.round((width * MAX_IMAGE_DIMENSION) / height);
      height = MAX_IMAGE_DIMENSION;
    }

    canvas.width = width;
    canvas.height = height;
    ctx.drawImage(image, 0, 0, width, height);
    return true;
  }

  canvas.width = width;
  canvas.height = height;
  ctx.drawImage(image, 0, 0);
  return false;
}

/**
 * Load image from blob
 */
export const loadImage = (file: Blob): Promise<HTMLImageElement> => {
  return new Promise((resolve, reject) => {
    const img = new Image();
    img.onload = () => resolve(img);
    img.onerror = reject;
    img.src = URL.createObjectURL(file);
  });
};

/**
 * Remove background from image using AI
 * @param imageElement - Image to process
 * @param onProgress - Progress callback (0-100)
 * @returns PNG blob with transparent background
 */
export const removeBackground = async (
  imageElement: HTMLImageElement,
  onProgress?: (progress: number) => void
): Promise<Blob> => {
  try {
    console.log("Starting background removal process...");
    onProgress?.(10);

    const segmenter = await pipeline(
      "image-segmentation",
      "Xenova/segformer-b0-finetuned-ade-512-512",
      {
        device: "webgpu",
      }
    );

    onProgress?.(30);

    // Convert HTMLImageElement to canvas
    const canvas = document.createElement("canvas");
    const ctx = canvas.getContext("2d");

    if (!ctx) throw new Error("Could not get canvas context");

    // Resize image if needed and draw it to canvas
    const wasResized = resizeImageIfNeeded(canvas, ctx, imageElement);
    console.log(
      `Image ${wasResized ? "was" : "was not"} resized. Final dimensions: ${canvas.width}x${canvas.height}`
    );

    onProgress?.(40);

    // Get image data as base64
    const imageData = canvas.toDataURL("image/jpeg", 0.8);
    console.log("Image converted to base64");

    onProgress?.(50);

    // Process the image with the segmentation model
    console.log("Processing with segmentation model...");
    const result = await segmenter(imageData);

    onProgress?.(70);

    console.log("Segmentation result:", result);

    if (!result || !Array.isArray(result) || result.length === 0 || !result[0].mask) {
      throw new Error("Invalid segmentation result");
    }

    // Create a new canvas for the masked image
    const outputCanvas = document.createElement("canvas");
    outputCanvas.width = canvas.width;
    outputCanvas.height = canvas.height;
    const outputCtx = outputCanvas.getContext("2d");

    if (!outputCtx) throw new Error("Could not get output canvas context");

    // Draw original image
    outputCtx.drawImage(canvas, 0, 0);

    onProgress?.(80);

    // Apply the mask
    const outputImageData = outputCtx.getImageData(
      0,
      0,
      outputCanvas.width,
      outputCanvas.height
    );
    const data = outputImageData.data;

    // Apply inverted mask to alpha channel
    for (let i = 0; i < result[0].mask.data.length; i++) {
      // Invert the mask value (1 - value) to keep the subject instead of the background
      const alpha = Math.round((1 - result[0].mask.data[i]) * 255);
      data[i * 4 + 3] = alpha;
    }

    outputCtx.putImageData(outputImageData, 0, 0);
    console.log("Mask applied successfully");

    onProgress?.(90);

    // Convert canvas to blob
    return new Promise((resolve, reject) => {
      outputCanvas.toBlob(
        (blob) => {
          if (blob) {
            console.log("Successfully created final blob");
            onProgress?.(100);
            resolve(blob);
          } else {
            reject(new Error("Failed to create blob"));
          }
        },
        "image/png",
        1.0
      );
    });
  } catch (error) {
    console.error("Error removing background:", error);
    throw error;
  }
};

/**
 * Remove background from file
 * @param file - Image file
 * @param onProgress - Progress callback
 * @returns PNG blob with transparent background
 */
export const removeBackgroundFromFile = async (
  file: File | Blob,
  onProgress?: (progress: number) => void
): Promise<Blob> => {
  const image = await loadImage(file);
  return removeBackground(image, onProgress);
};
